/**
activities
**/

define('forms/predefined/activities/controller',['require','text!forms/predefined/activities/view.html'],function (require) {
    var tpl = require("text!forms/predefined/activities/view.html");
   

    var definition = {
        template: tpl,
        build: function (vm, node, callback) {
            var built = _.template(tpl);
            var properties = {
                Disabled: node.disabled
            };

            $.extend(true, properties, node);

            callback(built(properties));
        }
    }

    return definition;

});
