/**
DOUBLE
**/

define('forms/fields/double/controller',['require','text!forms/fields/numeric/view.html'],function (require) {
    var tpl = require("text!forms/fields/numeric/view.html"); //use numeric template
   

    var definition = {
        template: tpl,
        build: function (vm, node, callback) {
            var built = _.template(tpl);

            //make sure we have all the vals set

            var properties = {
                //Required: false,
                DecimalPlaces: 19,
                MinValue: '',
                MaxValue: '',
                StepIncrement: 1,
                Disabled: node.disabled,
                Required: node.Required,
                Format:''
            };

            $.extend(true, properties, node);

            callback(built(properties));
        }
    }

    return definition;

});
